<template>
  <v-container>
    <v-layout text-center wrap>
      <v-flex mb-4>
        <h3 class="display-1 font-weight-bold mb-3">
          Price prediction for GS1200R ADV.
        </h3>
      </v-flex>

      <v-flex mb-5 xs12>
        <h2 class="headline font-weight-bold mb-3">Your inputs are:</h2>

        <v-layout justify-center>
          <v-form>
            <v-container>
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="year"
                    label="Year"
                    outlined
                    shaped
                  ></v-text-field>
                </v-col>
                <v-col>
                  <v-text-field
                    v-model="km"
                    label="Kilometer"
                    outlined
                    shaped
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-switch
                    v-model="accident"
                    inset
                    label="Accident?"
                  ></v-switch>
                </v-col>
                <v-col>
                  <v-switch
                    v-model="pannierSide"
                    inset
                    label="Pannier Side?"
                  ></v-switch>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-switch
                    v-model="pannierTop"
                    inset
                    label="Pannier Top?"
                  ></v-switch>
                </v-col>
                <v-col>
                  <v-switch v-model="navi" inset label="Navi?"></v-switch>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="prediction"
                    label="Price"
                    placeholder="Predicted Price"
                    outlined
                    shaped
                    readonly
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-btn block color="secondary" @click="startTraining"
                    >Training</v-btn
                  >
                </v-col>
                <v-col>
                  <v-btn block color="primary" @click="getPrediction"
                    >Prediction</v-btn
                  >
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-layout>
      </v-flex>
    </v-layout>
    <v-footer padless color="primary lighten-4">
      <v-col class="text-center" cols="12">
        {{ info }}
      </v-col>
    </v-footer>
  </v-container>
</template>

<script>
import { ref, onMounted } from "@vue/composition-api";
import * as ml5 from "ml5";
import csvPath from "./data/data.csv";

export default {
  name: "HelloWorld",
  setup() {
    const state = ref("collection");
    const year = ref(2015);
    const km = ref(28000);
    const accident = ref(false);
    const pannierSide = ref(true);
    const pannierTop = ref(true);
    const navi = ref(false);
    const prediction = ref(null);
    const model = ref(null);
    const info = ref(
      "Model Loaded! Ready to train. Please click Training button."
    );

    onMounted(() => {
      const options = {
        inputs: [
          "YEAR",
          "KM",
          "ACCIDENT",
          "PANNIER_SIDE",
          "PANNIER_TOP",
          "NAVI"
        ],
        outputs: ["PRICE"],
        task: "regression",
        debug: true
      };
      model.value = ml5.neuralNetwork(options, modelLoaded);
      csvPath.forEach(element => {
        const inputs = [
          element.YEAR,
          element.KM,
          element.ACCIDENT,
          element.PANNIER_SIDE,
          element.PANNIER_TOP,
          element.NAVI
        ];
        const outputs = [element.PRICE];
        model.value.addData(inputs, outputs);
      });
    });

    const modelLoaded = () => {
      //console.log("Model created!");
    };

    const startTraining = () => {
      if (state.value === "collection") {
        state.value = "training";
        info.value = "Training started!";
        model.value.normalizeData();
        let trainingOptions = {
          epochs: 200
        };
        model.value.train(trainingOptions, whileTraining, finishedTraining);
      } else {
        info.value = "Model already trained. Please prediction button!";
      }
    };

    const whileTraining = epoch => {
      info.value = `Training started! Epoch: ${epoch}`;
    };

    const finishedTraining = () => {
      state.value = "prediction";
      info.value = "Training finished! Click Prediction button.";
    };

    const getPrediction = () => {
      if (state.value === "prediction") {
        const inputs = [
          year.value,
          km.value,
          accident.value,
          pannierSide.value,
          pannierTop.value,
          navi.value
        ];
        model.value.predict(inputs, gotResults);
      } else {
        info.value = "Model is not trained. Please click Training button.";
      }
    };

    const gotResults = (err, results) => {
      if (err) {
        info.value = err;
      } else {
        prediction.value = Math.floor(results[0].value);
        info.value = `Prediction value is: ${prediction.value}`;
      }
    };

    return {
      info,
      state,
      year,
      km,
      accident,
      pannierSide,
      pannierTop,
      navi,
      prediction,
      startTraining,
      getPrediction
    };
  }
};
</script>
